import { Controller, Decoration } from '@ali/4ever-cangjie';
import { PASTE_DECORATION } from '../../utils/constants';

export default function setDecorations(
  controller: Controller,
  dec: Decoration,
): Controller {
  const decorations: Decoration[] = controller.value.decorations.filter(
    item => item.mark.type !== PASTE_DECORATION,
  );
  decorations.push(dec);
  return controller.setDecorations(decorations);
}
